




















































import Vue from 'vue'
import { getAllMenu, deleteMenu } from '@/services/menu'

export default Vue.extend({
    name: 'MenuIndex',
    data () {
        return {
            menus: [],
        }
    },
    created () {
        this.loadAllMenu()
    },
    methods: {
        async loadAllMenu () {
            const { data } = await getAllMenu()
            if (data.code === '000000') {
                this.menus = data.data
            }
        },
        async handleEdit (item: any) {
            this.$router.push({
                name: 'menu-edit',
                params: {
                    id: item.id,
                },
            })
        },
        handleDelete (item: any) {
            console.log(item)
            this.$confirm('确认删除吗?', '删除提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning',
            }).then(async () => {
                const { data } = await deleteMenu(item.id)
                if (data.code === '000000') {
                    this.$message.success('删除成功')
                    this.loadAllMenu()
                }
            })
        },
    },
})
